import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Work } from './work';
import 'rxjs/Rx';
import { HOST } from '../../../const';

@Injectable()
export class WorkService {

  constructor(private http: Http) { }

  getWorks() {
    return this.http.get(HOST + '/api/works')
      .map((response: Response) => response.json());
  }

  deleteWork(workId) {
    return this.http.delete(HOST + '/api/work/' + workId)
      .map((response: Response) => response.json());
  }
  addWork(newWork: Work){
    const bodyForSendingNewWork = JSON.stringify({
      imgPath: newWork.imgPath,
      link: newWork.link,
      name: newWork.name,
      technologies: newWork.technologies
    });
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    return this.http.post(HOST + '/api/work', bodyForSendingNewWork, options)
      .map((response: Response) => response.json());
  }
  updateWork(updatedWork: Work){
    const bodyForSendingUpdatedWork = JSON.stringify({
      imgPath: updatedWork.imgPath,
      link: updatedWork.link,
      name: updatedWork.name,
      technologies: updatedWork.technologies
    });
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    return this.http.put(HOST + '/api/work/' + updatedWork._id, bodyForSendingUpdatedWork, options)
      .map((response: Response) => response.json());
  }

}



