import {Component, OnInit} from '@angular/core';

import {ResumeService} from './resume.service';


@Component({
  selector: 'cv-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.css']
})
export class ResumeComponent implements OnInit {
  info = {};
  logo = {
    logoSrc: ''
  };
  constructor(private resumeService: ResumeService) {}

  ngOnInit() {
    this.getResume();
    this.getLogo();
  };
  getResume(){
    this.resumeService.getInfo()
      .subscribe((data) => {
        this.info = data[0];
        console.log(this.info);
      });
  }
  getLogo(){
    this.resumeService.getLogo()
      .subscribe((data) => this.logo = data[0]);
  }
  changeLogo($event){
    let file = $event.target.files[0];
    //this.fileLength = $event.target.files.length;
    let myReader = new FileReader();
    myReader.onloadend = () => {
      this.logo.logoSrc = 'data:image/jpg;base64,' + btoa(myReader.result);
      console.log(this.logo);
      this.resumeService.updateLogo(this.logo)
        .subscribe((data) => {
        console.log(data);
        this.getLogo();
      })
    };
    myReader.readAsBinaryString(file);
  }
}





