import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { HOST } from '../../../const';
import 'rxjs/Rx';

@Injectable()
export class ResumeService {

  constructor(private http: Http) { }

  getInfo() {
    return this.http.get(HOST +'/api/info')
      .map((response: Response) => response.json());
  }

  getLogo() {
    return this.http.get(HOST + '/api/logo')
      .map((response: Response) => response.json());
  }
  updateLogo(logo){
    const bodyForSendingNewLogo = JSON.stringify({
      logoSrc: logo.logoSrc,
    });
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    return this.http.put(HOST + '/api/logo/' + logo._id, bodyForSendingNewLogo, options)
      .map((response: Response) => response.json());
  }

}



