import { Component, OnInit } from '@angular/core';
import { Work } from './work';
import { WorkService } from './work.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import {ViewContainerRef} from '@angular/core';

declare let $: any;
@Component({
  selector: 'cv-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {
  works: Work[] = [];
  selectedWork: Work;
  workForm: FormGroup;
  newWork: Work;
  updatedWork: Work;
  imgPath = '';
  fileLength: number;
  action = '';
  modalName: string;
  constructor(private workService: WorkService, public toastr: ToastsManager, vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
    this.workForm = new FormGroup({
      'imgPath': new FormControl(''),
      'link': new FormControl('', Validators.required),
      'name': new FormControl('', Validators.required),
      'technologies': new FormControl('HTML,CSS,JavaScript', Validators.required)
    });
  }
  ngOnInit() {
    this.getAllworks()
  };
  getAllworks(){
    this.workService.getWorks()
      .subscribe((data) => {
        this.works = data;
        this.selectedWork = this.works[0];
        this.works[0].selected = true;
      });
  }
  deleteWork(workSelected){
    this.workService.deleteWork(workSelected._id)
      .subscribe(() => {
        this.getAllworks();
        this.toastr.success('Work ' + workSelected.name +' has been deleted!');
      })
  }
  getSelectedWork(workSelected: Work) {
    this.selectedWork = workSelected;
    this.selectedWorkHighlight();
  }
  openModal(action){
    this.action = action;
    if (action === 'adding'){
      this.imgPath = '';
      this.modalName = 'Add work';
      this.workForm.setValue({
        'imgPath': '',
        'link': '',
        'name': '',
        'technologies': ''
      })
    } else if (action === 'updating'){
      this.imgPath = this.selectedWork.imgPath;
      this.modalName = 'Update work';
      this.workForm.setValue({
        'imgPath': '',
        'link': this.selectedWork.link,
        'name': this.selectedWork.name,
        'technologies': this.selectedWork.technologies
      })
    }
    $('#workModal').modal('show');
  }
  actionWork(){
    if (this.action == 'adding'){
      this.addWork()
    } else {
      this.updateWork();
    }
  }
  addWork(){
    this.newWork = this.workForm.value;
    /*set img path*/
    if(!this.fileLength){
      this.newWork.imgPath = "../assets/none-image.jpg"
    } else {
      this.newWork.imgPath = this.imgPath;
    }
    this.newWork.technologies = this.newWork.technologies.split(',');
    this.workService.addWork(this.newWork)
      .subscribe(() => {
        this.getAllworks();
        $('#workModal').modal('hide');
        this.toastr.success('Work ' + this.newWork.name + ' has been added!');
        this.workForm.reset();
        this.imgPath = '';
      })
  }
  updateWork(){
    this.updatedWork = this.workForm.value;
    this.updatedWork._id = this.selectedWork._id;
    this.updatedWork.imgPath = this.selectedWork.imgPath;
    /*set img path*/
    this.updatedWork.imgPath = this.imgPath;
    if(this.workForm.value.imgPath){
      this.updatedWork.imgPath = this.workForm.value.imgPath;
    } else {
      this.updatedWork.imgPath = this.selectedWork.imgPath;
    }
    if(!(this.updatedWork.technologies instanceof Array)){
      this.updatedWork.technologies = this.updatedWork.technologies.split(',');
    }
    console.log(this.updatedWork);
    this.workService.updateWork(this.updatedWork)
      .subscribe(() => {
        this.getAllworks();
        $('#workModal').modal('hide');
        this.toastr.success('Work ' + this.updatedWork.name + ' has been updated!');
      }, (err) => {
        if(err.status = 413){
          this.toastr.error('Error, too large file. Try another file!');
        } else {
          this.toastr.error('Error, ' + err.status);
          $('#workModal').modal('hide');
        }
      })

  }
  previewFile($event) {
    let file = $event.target.files[0];
    this.fileLength = $event.target.files.length;
    let myReader = new FileReader();
    myReader.onloadend = () => {
      this.imgPath = 'data:image/jpg;base64,' + btoa(myReader.result);
    };
    myReader.readAsBinaryString(file);
  }
  selectedWorkHighlight() {
    for (let i = 0; i < this.works.length; i++ ) {
      (this.works[i]._id === this.selectedWork._id) ? this.works[i].selected = true : this.works[i].selected = false;
    }
  }
}








